import { Model } from './base/Model';
export class AppState extends Model {
    constructor() {
        super(...arguments);
        this._order = {
            payment: "",
            email: "",
            phone: "",
            address: "",
            items: [],
            total: 0
        };
        this._basket = [];
        this.formErrors = {};
    }
    set products(products) {
        this._products = products;
    }
    set order(order) {
        this._order = order;
    }
    get order() {
        return this._order;
    }
    get basket() {
        return this._basket;
    }
    get products() {
        return this._products;
    }
    set id(id) {
        this.preview = id;
    }
    get id() {
        return this.preview;
    }
    getProduct(cardId) {
        return this._products.find((item) => item.id === cardId);
    }
    set preview(cardId) {
        if (!cardId) {
            this._preview = null;
            return;
        }
        const selectedCard = this.getProduct(cardId);
        if (selectedCard) {
            this._preview = cardId;
        }
    }
    clearBasket() {
        this._basket.splice(0, this._basket.length);
    }
    toBasket(product) {
        if (!this._basket.includes(product)) {
            this._basket.push(product);
        }
    }
    deleteFromBasket(product) {
        let result = this.basket.filter(item => item.id !== product.id);
        this._basket = result;
    }
    setTotal() {
        return this.basket.reduce((sum, product) => sum + product.price, 0);
    }
    validateOrder() {
        const errors = {};
        if (!this._order.payment) {
            errors.payment = 'Необходимо указать тип оплаты';
        }
        if (!this._order.address) {
            errors.address = 'Необходимо указать адрес';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    validateContacts() {
        const errors = {};
        if (!this._order.email) {
            errors.email = 'Необходимо указать email';
        }
        if (!this._order.phone) {
            errors.phone = 'Необходимо указать телефон';
        }
        this.formErrors = errors;
        this.events.emit('formErrors:change', this.formErrors);
        return Object.keys(errors).length === 0;
    }
    setOrderField(field, value) {
        this._order[field] = value;
        this.validateOrder() && this.validateContacts();
    }
}
